// 
export const reqSearchproduct = (keyword, number, pageNo) =>{
	return uni.request({
		url:`http://localhost:3000/search?keyword=${keyword}&number=${number}&pageNo=${pageNo}`,
		method:"GET"
	})
}

// 
export const reqHomeproduct = (number, pageNo) =>{
	return uni.request({
		url:`http://localhost:3000/recBook?number=${number}&pageNo=${pageNo}`,
		method:"GET"
	})
}

